import React, { useState } from "react";
import s from "./SiteMap.module.scss";
import { Link } from "react-router-dom";
import { useMediaQuery } from "../Introduction/Philosophy/useMediaQuery";

export default function Sitemap() {

    type Section = 'introduction' | 'firstFloor' | 'secondFloor' | 'funeralGuide' | 'facilityGuide' | 'procedure' | 'info';
    
    const isMobile = useMediaQuery("(max-width: 1023px)");

    const [openSections, setOpenSections] = useState({
        introduction: false,
        firstFloor: false,
        secondFloor: false,
        funeralGuide: false,
        facilityGuide: false,
        procedure: false,
        info: false,
    });

    const toggleSection = (section: Section) => {
        setOpenSections((prevSections) => ({
            ...prevSections,
            [section]: !prevSections[section],
        }));
    };

    return (
        <>
          {isMobile ? (
                <main className={s.siteMapContainer}>
                {/*   */}
                <section className={s.box} onClick={() => toggleSection("introduction")}>
                    <div className={s.headBox}>
                        
                    </div>
                    {openSections.introduction && (
                        <div className={s.linkBox}>
                            <Link to="/introduction/directions"></Link>
                            <Link to="/introduction/greeting"></Link>
                            <Link to="/introduction/philosophy"> </Link>
                        </div>
                    )}
                </section>

                {/* 1  */}
                <section className={s.box} onClick={() => toggleSection("firstFloor")}>
                    <div className={s.headBox}>
                        1
                    </div>
                    {openSections.firstFloor && (
                        <div className={s.linkBox}>
                            <Link to="/facilities/1st/funeral-office"> </Link>
                            <Link to="/facilities/1st/preparation-room">, </Link>
                            <Link to="/facilities/1st/office"></Link>
                            <Link to="/facilities/1st/reception">,    </Link>
                            <Link to="/facilities/1st/family-waiting-room"> </Link>
                        </div>
                    )}
                </section>

                {/* 2  */}
                <section className={s.box} onClick={() => toggleSection("secondFloor")}>
                    <div className={s.headBox}>
                        2
                    </div>
                    {openSections.secondFloor && (
                        <div className={s.linkBox}>
                            <Link to="/facilities/2nd/mourning-room"></Link>
                            <Link to="/facilities/2nd/family-lounge"> </Link>
                            <Link to="/facilities/2nd/restaurant">, </Link>
                            <Link to="/facilities/2nd/cafe"></Link>
                        </div>
                    )}
                </section>

                {/*    */}
                <section className={s.box} onClick={() => toggleSection("funeralGuide")}>
                    <div className={s.headBox}>
                         
                    </div>
                    {openSections.funeralGuide && (
                        <div className={s.linkBox}>
                            <Link to="/usage/funeral/funeral-items"></Link>
                            <Link to="/usage/funeral/funeral-hall">, ()</Link>
                            <Link to="/usage/funeral/memorial-engraving">, ,  </Link>
                            <Link to="/usage/funeral/memorial-ceremony">(49, )</Link>
                            <Link to="/usage/funeral/dining-facilities">, (), </Link>
                        </div>
                    )}
                </section>

                {/*    */}
                <section className={s.box} onClick={() => toggleSection("facilityGuide")}>
                    <div className={s.headBox}>
                         
                    </div>
                    {openSections.facilityGuide && (
                        <div className={s.linkBox}>
                            <Link to="/usage/facilities/crematorium"></Link>
                            <Link to="/usage/facilities/natural-burial">(, )</Link>
                            <Link to="/usage/facilities/memorial-house"> </Link>
                            <Link to="/usage/facilities/memorial-park"></Link>
                        </div>
                    )}
                </section>

                {/*   */}
                <section className={s.box} onClick={() => toggleSection("procedure")}>
                    <div className={s.headBox}>
                        
                    </div>
                    {openSections.procedure && (
                        <div className={s.linkBox}>
                            <Link to="/guide/procedure/before-death"> </Link>
                            <Link to="/guide/procedure/after-death"> </Link>
                            <Link to="/guide/procedure/after-funeral"> </Link>
                        </div>
                    )}
                </section>

                {/*    */}
                <section className={s.box}  onClick={() => toggleSection("info")}>
                    <div className={s.headBox}>
                         
                    </div>
                    {openSections.info && (
                        <div className={s.linkBox}>
                            <Link to="/guide/info"></Link>
                            <Link to="/guide/info/ancestral-rites"></Link>
                        </div>
                    )}
                </section>
            </main>

          ) : (
            
            <main className={s.siteMapContainer}>
                <section className={s.box}>
                    <div className={s.headBox}></div>
                    <div className={s.linkBox}>
                        <Link to="/introduction/directions"></Link>
                        <Link to="/introduction/greeting"></Link>
                        <Link to="/introduction/philosophy"> </Link>
                    </div>
                </section>

                <section className={s.box}>
                    <div className={s.headBox}>1</div>
                    <div className={s.linkBox}>
                        <Link to="/facilities/1st/funeral-office"> </Link>
                        <Link to="/facilities/1st/preparation-room">, </Link>
                        <Link to="/facilities/1st/office"></Link>
                        <Link to="/facilities/1st/reception">,     </Link>
                        <Link to="/facilities/1st/family-waiting-room"> </Link>
                    </div>
                </section>

                <section className={s.box}>
                    <div className={s.headBox}>2</div>
                    <div className={s.linkBox}>
                        <Link to="/facilities/2nd/mourning-room"></Link>
                        <Link to="/facilities/2nd/family-lounge"> </Link>
                        <Link to="/facilities/2nd/restaurant">, </Link>
                        <Link to="/facilities/2nd/cafe"></Link>
                    </div>
                </section>

                <section className={s.box2}>
                    <div className={s.headBox}> </div>
                    <div className={s.linkBox}>
                        <Link to="/usage/funeral/funeral-items"></Link>
                        <Link to="/usage/funeral/funeral-hall">, ()</Link>
                        <Link to="/usage/funeral/memorial-engraving">, ,  </Link>
                        <Link to="/usage/funeral/memorial-ceremony">(49, )</Link>
                        <Link to="/usage/funeral/dining-facilities">, (), </Link>
                    </div>
                </section>

                <section className={s.box}>
                    <div className={s.headBox}> </div>
                    <div className={s.linkBox}>
                        <Link to="/usage/facilities/crematorium"></Link>
                        <Link to="/usage/facilities/natural-burial">(, )</Link>
                        <Link to="/usage/facilities/memorial-house"> </Link>
                        <Link to="/usage/facilities/memorial-park"></Link>
                    </div>
                </section>

                <section className={s.box}>
                    <div className={s.headBox}></div>
                    <div className={s.linkBox}>
                        <Link to="/guide/procedure/before-death"> </Link>
                        <Link to="/guide/procedure/after-death"> </Link>
                        <Link to="/guide/procedure/after-funeral"> </Link>
                    </div>
                </section>

                <section className={s.box}>
                    <div className={s.headBox}> </div>
                    <div className={s.linkBox}>
                        <Link to="/guide/info"></Link>
                        <Link to="/guide/info/ancestral-rites"></Link>
                    </div>
                </section>
            </main>
          )}
        </>
      );
    }
            
    