import { useEffect } from "react";
import styles from "./Dicrections.module.scss";

declare global {
  interface Window {
    kakao: any;
  }
}

const DirectionPage = () => {
  useEffect(() => {
    let container = document.getElementById(`map`); //    DOM 
    let options = {
      center: new window.kakao.maps.LatLng(
        35.470755896136545,
        129.13367039981992
      ), //   
      level: 3, //  (,  )
    };

    let map = new window.kakao.maps.Map(container, options); //     
    const markerPosition = new window.kakao.maps.LatLng(
      35.470755896136545,
      129.13367039981992
    );

    //  
    const marker = new window.kakao.maps.Marker({
      position: markerPosition,
    });

    //     
    marker.setMap(map);
  }, []);

  return (
    <div className={styles.container}>
      <div className={styles.callout}>
        <p className={styles.highlight_darken}>  </p>
        <p>
               550, <br />
          ()     1532
        </p>
      </div>
      <div>
        <h2 className={styles.subheading}> </h2>
        <div className={styles.content}>
          <a className={styles.redirection} href="https://its.ulsan.kr/">
              
          </a>
          <table className={styles.table}>
            <thead>
              <tr>
                <th colSpan={2}>
                   <span className={styles.highlight}>952</span>{" "}
                   (  )
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td></td>
                <td>
                   -  -  -  -  -
                   -  -  -  -  -  -
                  () -  -  - 
                  -  -  - 
                </td>
              </tr>
              <tr>
                <td> </td>
                <td>
                  <div className={styles.time}>
                    <span>06:00</span>
                    <span>08:40</span>
                    <span>11:30</span>
                    <span>14:20</span>
                    <span>17:20</span>
                  </div>
                </td>
              </tr>
              <tr>
                <td> </td>
                <td>
                  <div className={styles.time}>
                    <span>07:20</span>
                    <span>10:10</span>
                    <span>13:00</span>
                    <span>16:00</span>
                    <span>18:50</span>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className={styles.subheading}> </div>
        <div className={styles.content} style={{ paddingBottom: "20px" }}>
          <div className={styles.path}>
            <h3></h3>
            <p>
                     IC() 
              34()     
              
            </p>
          </div>
          <div className={styles.path}>
            <h3></h3>
            <p>
                      SDI  
              34()     
              
            </p>
          </div>
          <div className={styles.path}>
            <h3></h3>
            <p>
                       
              
            </p>
          </div>
        </div>
        <div id="map" className={styles.map}></div>
      </div>
    </div>
  );
};

export default DirectionPage;
