import React from "react";
import Slider from "react-slick";
import RoomInfoCard from "components/RoomInfoCard/RoomInfoCard";
import Information from "components/information/Information";
import FacilityCarousel from "components/facility/FacilityCarousel";
import MainImage from "components/mainImage/MainImage";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import styles from "./MainPage.module.scss";

const MainPage: React.FC = () => {
  const today = new Date();
  const week = ["", "", "", "", "", "", ""];
  const formattedDate = `${today.getFullYear()}. ${
    today.getMonth() + 1
  }. ${today.getDate()}.(${
    week[today.getDay()]
  })  ${today.getHours()} ${today.getMinutes()}`;

  const settings = {
    dots: true,
    infinite: true,
    speed: 1000,
    arrows: true,
    slidesToShow: 3,
    slidesToScroll: 3,
    variableWidth: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          variableWidth: false,
          arrows: false,
        },
      },
    ],
  };

  return (
    <div className={styles.container}>
      <MainImage />
      <div className={styles.roomInfoCardBackground}>
        <div className={styles.roomInfoCardSlide}>
          <div className={styles.timeTitle}> {formattedDate}</div>
          <div className={styles.mainTitle}>
               
          </div>
          <div className={styles.sliderContainer}>
            <Slider {...settings}>
              <div className={styles.slideItem}>
                <RoomInfoCard
                  deceasedName=""
                  roomNumber={101}
                  chiefMourner={[" : ", " : , "]}
                  burialSite="(2)"
                  casketingTime="2024-05-16 10:00"
                  funeralTime="2024-05-17 11:30"
                />
              </div>
              <div className={styles.slideItem}>
                <RoomInfoCard
                  deceasedName=""
                  roomNumber={202}
                  chiefMourner={[
                    " : ",
                    " : , , ",
                    " : ",
                    ": , ",
                  ]}
                  burialSite="(3)"
                  casketingTime="2024-05-16 10:00"
                  funeralTime="2024-05-17 11:30"
                />
              </div>
              <div className={styles.slideItem}>
                <RoomInfoCard />
              </div>
              <div className={styles.slideItem}>
                <RoomInfoCard />
              </div>
              <div className={styles.slideItem}>
                <RoomInfoCard />
              </div>
              <div className={styles.slideItem}>
                <RoomInfoCard />
              </div>
            </Slider>
          </div>
        </div>
      </div>
      <div className={styles.facility}>
        <div className={styles.subTitle}>   </div>
        <div className={styles.mainTitle}>   </div>
        <FacilityCarousel />
      </div>
      <div className={styles.informationBackground}>
        <div className={styles.information}>
          <div className={styles.subTitle}>  </div>
          <div className={styles.mainTitle}>
               
          </div>
          <Information />
        </div>
      </div>
    </div>
  );
};

export default MainPage;
