import { useEffect, useState } from "react";
import styles from "./FuneralItems.module.scss";
import { FuneralData } from "./funeralItemsData";

const FuneralItems: React.FC = () => {
  const [selectedName, setSeletedName] = useState("");

  //  ,  name 
  const handleSelect = (name: string) => {
    setSeletedName(name);
  };

  interface TableStyles {
    name: string;
    className: string;
  }

  //*    
  const tableStyles: TableStyles[] = [
    {
      name: "",
      className: styles.table_coffin,
    },
    {
      name: "",
      className: styles.table_shroud,
    },
    {
      name: " ",
      className: styles.table_deceased,
    },
    {
      name: " ",
      className: styles.table_mourner,
    },
    {
      name: "",
      className: styles.table_miscellaneous,
    },
    {
      name: " ",
      className: styles.table_modern,
    },
    {
      name: "",
      className: styles.table_enshrouding,
    },
  ];

  return (
    <main className={styles.container}>
      <section>
        <div className={styles.callout}>24  </div>
      </section>

      <section className={styles.description_container}>
        <h3 className={styles.subheading}> </h3>
        <ul className={styles.description}>
          <li>
            <strong>:</strong> , , , , , ,
            , , , ,  {" "}
          </li>
          <li>
            <strong>:</strong>  , (), , ,
            ,  
          </li>
        </ul>
      </section>

      <section className={styles.table_all_container}>
        <ul className={styles.select_container}>
          <li
            className={`${styles.select_name} 
                        ${selectedName === "" ? styles.selected : ""}`}
            onClick={() => {
              handleSelect("");
            }}
          >
            
          </li>
          <li
            className={`${styles.select_name} 
                        ${selectedName === "" ? styles.selected : ""}`}
            onClick={() => {
              handleSelect("");
            }}
          >
            
          </li>
          <li
            className={`${styles.select_name} 
                        ${selectedName === " " ? styles.selected : ""}`}
            onClick={() => {
              handleSelect(" ");
            }}
          >
             
          </li>
          <li
            className={`${styles.select_name} 
                        ${selectedName === " " ? styles.selected : ""}`}
            onClick={() => {
              handleSelect(" ");
            }}
          >
             
          </li>
          <li
            className={`${styles.select_name} 
                        ${selectedName === "" ? styles.selected : ""}`}
            onClick={() => {
              handleSelect("");
            }}
          >
            
          </li>
          <li
            className={`${styles.select_name} 
                        ${selectedName === " " ? styles.selected : ""}`}
            onClick={() => {
              handleSelect(" ");
            }}
          >
             
          </li>
          <li
            className={`${styles.select_name} 
                        ${selectedName === "" ? styles.selected : ""}`}
            onClick={() => {
              handleSelect("");
            }}
          >
            
          </li>
        </ul>

        {["", "", " ", " ", ""].includes(
          selectedName
        ) ? (
          <section className={styles.table_container}>
            <table
              className={
                tableStyles.find((style) => style.name === selectedName)
                  ?.className
              }
            >
              <thead>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </thead>
              <tbody>
                {/*  object title    .    . */}
                {FuneralData.find(
                  (item) => item.title === selectedName
                )?.item.map((item, index) => (
                  <tr>
                    <td>{item.id}</td>
                    <td>{item.name}</td>
                    <td>{item.standard}</td>
                    <td>{item.unit}</td>
                    <td>{item.price}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </section>
        ) : selectedName === " " ? (
          <section className={styles.table_container}>
            <table className={styles.table_modern}>
              <thead>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </thead>
              <tbody>
                {/*  object title    .    . */}
                {FuneralData.find(
                  (item) => item.title === selectedName
                )?.item.map((item, index) => (
                  <tr>
                    {item.category && <td rowSpan={3}>{item.category}</td>}
                    <td>{item.id}</td>
                    <td>{item.name}</td>
                    <td>{item.unit}</td>
                    <td>{item.price}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </section>
        ) : selectedName === "" ? (
          <section className={styles.table_container}>
            <table className={styles.table_enshrouding}>
              <thead>
                <th colSpan={2}></th>
                <th></th>
                <th></th>
                <th colSpan={2}></th>
              </thead>
              <tbody>
                <tr>
                  <td rowSpan={2} colSpan={2}>
                    
                  </td>
                  <td rowSpan={2}></td>
                  <td rowSpan={2}> </td>
                  <td></td>
                  <td>200,000</td>
                </tr>
                <tr>
                  <td></td>
                  <td>250,000</td>
                </tr>
              </tbody>
            </table>
          </section>
        ) : null}
      </section>
    </main>
  );
};

export default FuneralItems;
