import logo from "./logo.svg";
import menu from "./menu.svg";
import close from "./close.svg";

import { useNavigate } from "react-router-dom";
import { useEffect, useRef } from "react";

interface SubMenu {
  title: { name: string; url: string };
  items?: { name: string; url: string }[];
}

interface MenuItem {
  title: { name: string; url: string };
  subMenus?: SubMenu[];
}
const menuItems: MenuItem[] = [
  {
    title: { name: " ", url: "/introduction/philosophy" },
    subMenus: [
      {
        title: { name: "", url: "/introduction/philosophy" },
        items: [
          { name: "", url: "/introduction/philosophy" },
          { name: "", url: "/introduction/greeting" },
          { name: "", url: "/introduction/directions" },
        ],
      },
    ],
  },
  {
    title: { name: " ", url: "/facilities/1st/funeral-office" },
    subMenus: [
      {
        title: { name: "1", url: "/facilities/1st/funeral-office" },
        items: [
          { name: "", url: "/facilities/1st/funeral-office" },
          { name: ", ", url: "/facilities/1st/preparation-room" },
          { name: "", url: "/facilities/1st/office" },
          {
            name: ",    ",
            url: "/facilities/1st/reception",
          },
          { name: " ", url: "/facilities/1st/family-waiting-room" },
        ],
      },
      {
        title: { name: "2", url: "/facilities/2nd/mourning-room" },
        items: [
          { name: "", url: "/facilities/2nd/mourning-room" },
          { name: " ", url: "/facilities/2nd/family-lounge" },
          { name: ", ", url: "/facilities/2nd/restaurant" },
          { name: "", url: "/facilities/2nd/cafe" },
        ],
      },
    ],
  },
  {
    title: { name: "", url: "/usage/funeral/funeral-items" },
    subMenus: [
      {
        title: {
          name: " ",
          url: "/usage/funeral/funeral-items",
        }, // 
        items: [
          { name: "", url: "/usage/funeral/funeral-items" },
          { name: ", ()", url: "/usage/funeral/funeral-hall" },
          {
            name: ", ,  ",
            url: "/usage/funeral/memorial-engraving",
          },
          {
            name: "(49, )",
            url: "/usage/funeral/memorial-ceremony",
          },
          {
            name: ", (), ",
            url: "/usage/funeral/dining-facilities",
          },
        ],
      },
      {
        title: {
          name: " ",
          url: "/usage/facilities/crematorium",
        }, // 
        items: [
          { name: "", url: "/usage/facilities/crematorium" },
          {
            name: "(, )",
            url: "/usage/facilities/natural-burial",
          },
          { name: " ", url: "/usage/facilities/memorial-house" },
          { name: "", url: "/usage/facilities/memorial-park" },
        ],
      },
    ],
  },
  {
    title: { name: "", url: "/guide/procedure/before-death" },
    subMenus: [
      {
        title: { name: "", url: "/guide/procedure" },
        items: [
          { name: " ", url: "/guide/procedure/before-death" },
          { name: " ", url: "/guide/procedure/after-death" },
          { name: " ", url: "/guide/procedure/after-funeral" },
        ],
      },
      {
        title: { name: " ", url: "/guide/info" },
        items: [
          { name: "", url: "/guide/info" },
          { name: "", url: "/guide/info/ancestral-rites" },
        ],
      },
    ],
  },
  {
    title: { name: "", url: "/notices" },
  },
];

const Topbar: React.FC = () => {
  //  hover   
  const [hoveredMenuIndex, setHoveredMenuIndex] = useState<number | null>(null);

  const navigate = useNavigate();

  //   
  const handleLogoClick = (): void => {
    navigate("/");
  };

  //*  
  //     
  const [isOpen, setIsOpen] = useState(false);

  //  :  
  const handleOpenSubMenu = () => {
    if (window.innerWidth < 1024) {
      //   ,   ,
      setIsOpen(!isOpen);
    } else {
      //   .
      navigate("/sitemap");
    }
  };

  return (
    <header className={styles.topbar_container}>
      <nav className={styles.navbar_conainer}>
        {/* <nav className={styles.logo_nav_container}> */}
        <section className={styles.logo_container}>
          <img
            className={styles.logo_img}
            onClick={handleLogoClick}
            src={logo}
            alt="()"
          />
        </section>

        {/* 
            // main_container ?
          */}
        <section className={styles.main_container}>
          <ul
            className={`${styles.menu_all_container} ${
              isOpen && window.innerWidth < 1024 ? styles.block : ""
            }`}
          >
            {menuItems.map((menuItem, index) => (
              <li
                key={index}
                className={styles.menus_container}
                onMouseEnter={() => {
                  setHoveredMenuIndex(index);
                }}
                onMouseLeave={() => {
                  setHoveredMenuIndex(null);
                }}
              >
                <span //  ,  ,  , , 
                  className={styles.menu_title}
                >
                  <a
                    onClick={() => {
                      navigate(menuItem.title.url);
                    }}
                  >
                    {" "}
                    {menuItem.title.name}
                  </a>
                </span>

                {menuItem.subMenus ? ( // *     ()
                  <ul className={styles.sub_menus}>
                    {menuItem.subMenus?.map((subMenu, subIndex) => (
                      <li key={subIndex} className={styles.sub_sub_menus}>
                        {subMenu.title && (
                          <span className={styles.sub_menu_title}>
                            {subMenu.title.name}
                          </span>
                        )}
                        <ul className={styles.sub_menu_items}>
                          {subMenu.items?.map((item, itemIndex) => (
                            <li
                              key={itemIndex}
                              className={styles.sub_menu_item}
                            >
                              <a
                                onClick={() => {
                                  navigate(item.url);
                                  setIsOpen(false);
                                }}
                              >
                                {item.name}
                              </a>
                            </li>
                          ))}
                        </ul>
                      </li>
                    ))}
                  </ul>
                ) : window.innerWidth < 1024 ? ( //     .
                  <ul className={styles.sub_menus}></ul>
                ) : null}
                {/* todo :     */}
              </li>
            ))}
          </ul>
        </section>
        {/* </nav> */}

        <section className={styles.add_menu_container}>
          <img
            onClick={handleOpenSubMenu}
            className={styles.menu_img}
            src={window.innerWidth < 1024 && isOpen ? close : menu}
            alt="()"
          />
        </section>
      </nav>
    </header>
  );
};

export default Topbar;
